#new-landing .text-container {
	p {
		font: 300 16px 'Inter', sans-serif;
		margin-bottom: 20px;
		margin-bottom: 55px; }
	ol {
		li {
			list-style-type: decimal; } }
	ul {
		li {
			list-style-type: disc; } }
	ol,
	ul {
		padding-left: 20px;
		font: 16px/1.4 'Inter', sans-serif;
		margin-bottom: 20px;
		@include mq($max-width: 700px) {
			font-size: 14px; } }
	h1 {
		font: bold 26px/1.4 'Inter', sans-serif;
		margin-bottom: 25px;
		@include mq($max-width: 700px) {
			font-size: 22px; } }
	h2 {
		font: bold 20px/1.4 'Inter', sans-serif;
		margin-bottom: 25px;
		@include mq($max-width: 700px) {
			font-size: 18px; } }
	h3 {
		font: bold 18px/1.4 'Inter', sans-serif;
		margin-bottom: 25px;
		@include mq($max-width: 700px) {
			font-size: 16px; } }
	p {
		font: 16px/1.4 'Inter', sans-serif;
		margin-bottom: 15px;
		@include mq($max-width: 700px) {
			font-size: 14px; } } }
