#new-landing .folder-protect {
	position: relative;
	padding: 130px 0 75px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	flex-wrap: wrap;
	flex-direction: row;
	@include mq($max-width: 1500px) {
		padding: 50px 0; }
	@include mq($max-width: 1200px) {
		padding: 35px 0; }
	.inner {
		width: 40%;
		// width: 100%
		display: flex;
		flex-direction: column;
		align-items: center;
		text-align: center;
		@include mq($max-width: 1200px) {
			width: 100%;
			margin: 25px 0; } }
	.caption {
		margin-bottom: 25px;
		text-align: center;
		display: flex;
		flex-direction: column;
		align-items: center;
		&__subtitle {
			margin-bottom: 0; } }
	.icons {
		width: 100%;
		display: flex;
		// justify-content: space-between
		justify-content: center;
		position: relative;
		z-index: 2;
		@include mq($max-width: 575px) {
			flex-wrap: wrap;
			justify-content: center; } }
	&__img {
		display: block;
		width: 30%;
		&.left {
			clip-path: polygon(0 0, 100% 0%, 75% 100%, 0% 100%);
			@include mq($max-width: 1200px) {
				margin-right: auto; } }
		&.right {
			clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 0% 100%);
			@include mq($max-width: 1200px) {
				margin-left: auto; } }
		@include mq($max-width: 1200px) {
			width: 80%; } }
	.item {
		// margin: 10px
		margin: 1%;
		display: flex;
		flex-direction: column;
		align-items: center;
		text-align: center;
		width: 24%;
		@include mq($max-width: 1700px) {
			width: 31%; }
		@include mq($max-width: 575px) {
			width: 48%;
			margin: 1%;
			margin-bottom: 15px; }
		&__icon {
			display: block;
			width: 100px;
			height: auto;
			margin-bottom: 15px; }
		&__text {
			font: 300 14px 'Inter', sans-serif; } } }
