#new-landing .folder-complication {
	padding: 65px 0 150px;
	@include mq($max-width: 1200px) {
		padding: 50px 0 100px; }
	@include mq($max-width: 575px) {
		padding: 35px 0; }
	.caption {
		margin-bottom: 100px;
		text-align: center;
		@include mq($max-width: 1200px) {
			margin-bottom: 50px; }
		&__line {
			margin-left: auto;
			margin-right: auto; } }
	.inner {
		display: flex;
		align-items: center;
		justify-content: space-between;
		@include mq($max-width: 1000px) {
			flex-wrap: wrap; } }
	.item {
		position: relative;
		width: 520px;
		height: auto;
		@include mq($max-width: 1500px) {
			width: 420px; }
		@include mq($max-width: 1200px) {
			width: 50%; }
		@include mq($max-width: 575px) {
			width: 100%; }
		&:first-child {
			@include mq($max-width: 575px) {
				margin-bottom: 50px; }
			.item__img {
				transform: rotate(-15deg); }
			.item__circle {
				left: 0;
				bottom: 0;
				transform: translate(0, 50%); } }
		&:last-child {
			.item__img {
				transform: rotate(15deg); }
			.item__circle {
				right: 0;
				bottom: 0;
				transform: translate(0, 50%); }
			.item__text {
				text-align: right;
				transform: rotate(12deg);
				span {
 } } }					// color: $blue
		&__img {
			display: block;
			width: 100%; }
		&__circle {
			display: block;
			width: 300px;
			height: 300px;
			position: absolute;
			z-index: 2;
			@include mq($max-width: 1500px) {
				width: 220px;
				height: 220px; }
			@include mq($max-width: 700px) {
				width: 140px;
				height: 140px; }
			@include mq($max-width: 400px) {
				width: 100px;
				height: 100px; } }
		&__text {
			font: 700 30px 'Inter', sans-serif;
			transform: rotate(-15deg);
			@include mq($max-width: 700px) {
				font-size: 20px; }
			span {
				font-size: 70px;
				// color: $red
				color: $black;
				font-weight: 800;
				@include mq($max-width: 700px) {
					font-size: 30px; } } } } }
