		.window {
			position: absolute;
			z-index: 10;
			left: 0;
			top: 50%;
			left: 50%;
			width: 440px;
			background: $white;
			box-shadow: 0px 4px 25px #BFC2C9;
			border-radius: 5px;
			padding: 15px 15px;
			display: none;
			@include mq($max-width: 1200px) {
				position: static;
				width: 100%;
				padding: 20px;
				display: block;
				transform: translate(0,0) rotate(0) !important; }
			@include mq($max-width: 575px) {
				padding: 10px;
				background: none;
				box-shadow: none; }
			&-top {
				display: flex;
				align-items: center;
				margin-bottom: 15px;
				@include mq($max-width: 575px) {
					flex-wrap: wrap; } }
			&__label {
				margin-right: 15px;
				width: 50px;
				height: auto;
				display: block;
				@include mq($max-width: 1200px) {
					display: none; } }
			&__title {
				font: 700 14px 'Inter', sans-serif;
				@include mq($max-width: 575px) {
					width: 100%; } }
			&__text {
				font: 300 12px/1.6 'Inter', sans-serif;
				margin-bottom: 10px; }
			&__img {
				display: block;
				width: 100%;
				height: auto;
				margin: 0 auto; } }
		&__label {
			display: block;
			position: absolute;
			z-index: 2;
			top: 0;
			left: 0;
			width: 75px;
			height: auto;
			@include mq($max-width: 1200px) {
				transform: rotate(0) !important;
				width: auto; }
			@include mq($max-width: 575px) {
				width: 70px;
				left: 10px;
				top: 10px; } }
		&__clipper {
			display: block;
			width: 140%;
			height: auto;
			position: absolute;
			z-index: 1;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			transition: all .3s ease-out;
			@include mq($max-width: 1200px) {
				position: static;
				transform: translate(0,0) rotate(0) !important;
				height: 450px;
				width: auto !important;
				margin-left: auto;
				margin-right: auto; }
			@include mq($max-width: 575px) {
				// max-height: 250px
				height: auto;
				width: 90% !important;
				// max-width: 100% !important
				transform: rotate(0) !important;
				margin: 0 auto; } } } }
