	// 	display: block
	// 	width: 100%
	// 	@include mq($max-width: 1200px)
	// 		width: calc( 100% - 100px )
	// 	@include mq($max-width: 1000px)
	// 		width: 100%

	.caption {
		width: 870px;
		text-align: center;
		display: flex;
		align-items: center;
		flex-direction: column;
		margin: 0 auto;
		// height: auto
		// position: absolute
		// right: 100px
		// top: -50px
		// @include mq($max-width: 1200px)
		/// width: 400px
		/// top: -50px
		/// right: 15px
		@include mq($max-width: 1000px) {
			// position: static
			width: 100%;
			margin-bottom: 15px; } } }
