#new-landing .mainblock-glue {
	position: relative;
	margin-bottom: 80px;
	@include mq($max-width: 800px) {
		margin-bottom: 20px; }
	.btn1:after {
		@include mq($max-width: 400px) {
			display: none; } }
	&:after {
		content: '';
		display: block;
		width: 100%;
		height: 195px;
		background: url($imgUrl+'mainblock-glue/bg-line.png') repeat-x left bottom / contain;
		position: absolute;
		left: 0;
		bottom: 0;
		z-index: -1; }
	.c4 {
		position: absolute;
		left: 45%;
		bottom: 0;
		width: 250px;
		display: block;
		height: auto;
		@include mq($max-width: 1200px) {
			display: none; } }
	.c5 {
		position: absolute;
		right: -120px;
		bottom: 0;
		width: 100px;
		display: block;
		height: auto;
		@include mq($max-width: 1200px) {
			display: none; } }
	.container {
		min-height: 575px;
		position: relative;
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		padding: 65px 15px;
		max-width: 960px;
		@include mq($max-width: 700px) {
			min-height: 400px; }
		@include mq($max-width: 550px) {
			min-height: 350px; } }
	.bg {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		display: block;
		height: auto;
		z-index: -1; }
	.inner {
		@include mq($max-width: 1000px) {
			width: 60%; }
		@include mq($max-width: 550px) {
			width: 50%; } }
	.crystal-wrap {
		position: relative;
		&:after {
			content: "";
			position: absolute;
			top: 0;
			left: 0em;
			width: 100%;
			height: 100%;
			background: linear-gradient(to left, rgba(121, 117, 117, 0), rgba(255,255,255,.8), rgba(121, 117, 117, 0)) no-repeat -4em 0%;
			background-size: 4em 100%;
			-moz-transform: skewX(40deg);
			-o-transform: skewX(40deg);
			-webkit-transform: skewX(40deg);
			transform: skewX(40deg); }
		&.active {
			&:after {
				transition: 1.5s linear;
				background-position: 130% 0%; } } }
	.crystal {
		display: block;
		width: 600px;
		height: auto;
		margin: 5px 0 15px;
		@include mq($max-width: 1000px) {
			width: 100%; } }
	h1 {
		font: 500 24px 'Inter', sans-serif;
		position: relative;
		@include mq($max-width: 550px) {
			font-size: 16px; }
		@include mq($max-width: 400px) {
			font-size: 14px; }
		&:after {
			content: '';
			font-size: 16px;
			display: inline-block;
			margin-top: -2px;
			margin-left: 5px;
			vertical-align: top; } }
	h2 {
		font: 700 24px 'Inter', sans-serif;
		max-width: 475px;
		margin-bottom: 55px;
		@include mq($max-width: 550px) {
			font-size: 16px; }
		@include mq($max-width: 400px) {
			font-size: 14px; } }
	.btns {
		text-align: left;
		transform: translate(0, 80%); }
	.glues {
		margin-bottom: -40px;
		@include mq($max-width: 1000px) {
			width: 40%; }
		@include mq($max-width: 700px) {
			text-align: right; }
		@include mq($max-width: 700px) {
			width: 50%; }
		img {
			vertical-align: bottom;
			margin: 0 10px;
			@include mq($max-width: 1000px) {
				margin: 0 1%; } }
		.small {
			@include mq($max-width: 1000px) {
				width: 25%; }
			@include mq($max-width: 550px) {
				width: 35%; } }
		.big {
			@include mq($max-width: 1000px) {
				width: 40%; }
			@include mq($max-width: 550px) {
				width: 55%; } } }
	.prlx {
		position: absolute;
		z-index: 0;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		.c1,
		.c2,
		.c3 {
			position: absolute; }
		.c1 {
			top: 30% !important;
			right: auto !important;
			bottom: auto !important;
			left: 10% !important;
			@include mq($max-width: 1500px) {
				left: 5% !important; }
			@include mq($max-width: 1200px) {
				left: 2% !important;
				top: 10% !important; }
			@include mq($max-width: 700px) {
				left: 40% !important; }
			@include mq($max-width: 550px) {
				left: 5% !important; }
			@include mq($max-width: 370px) {
				top: 0 !important; } }
		.c2 {
			top: auto !important;
			right: auto !important;
			bottom: -10% !important;
			left: 40% !important;
			@include mq($max-width: 550px) {
				right: auto !important;
				left: 5% !important; } }
		.c3 {
			top: 50% !important;
			right: 2% !important;
			bottom: auto !important;
			left: auto !important;
			@include mq($max-width: 1200px) {
				top: 16% !important;
				right: 30% !important; }
			@include mq($max-width: 550px) {
				left: 30% !important;
				right: auto !important; }
			@include mq($max-width: 370px) {
				top: 2% !important; } } } }
