// Colors
$white: #fff;
$black: #000;

$red: #E91C2E;
$red2: #7C1622;
$orange: #B17C48;
$green2: #4AD02B;
$blue: #1F66B8;
$blue2: #2F3E67;
$light-blue: #96C4E5;
$lilac: #C4ADF3;
$pink: #F79DB9;
$yellow: #EAE3AC;
$dark: #393939;
$dark2: #343434;
$gray1: #555555;
$gray2: #B4B4B4;
$gray3: #DEDEDE;
$gray4: #BDBDBD;
$gray5: #5A5A5A;


$imgUrl: '../img/';

// Reset style
* {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
	outline: 0;
	border: 0;
	&:before,
	&:after {
		padding: 0;
		margin: 0;
		outline: 0;
		border: 0; } }


button,
html [type="button"], /* 1 */ {}
[type="reset"],
[type="submit"] {
	-webkit-appearance: button /* 2 */;
	background: none; }


input[type="text"],
input[type="tel"],
input[type="email"],
input[type="submit"],
input[type="number"],
input[type="search"] {
	-webkit-appearance: none; }


ul,
ol,
nav {
	list-style-type: none;
	li {
		list-style-type: none; } }

a {
	display: inline-block;
	text-decoration: none;
	transition: all .3s ease-out; }

html,
body {
	color: $black;
	height: 100%;
	display: flex;
	flex-direction: column; }

