#new-landing .site-block {
	padding: 50px 0;
	@include mq($max-width: 550px) {
		padding: 25px 0; } }

#new-landing .caption {
	margin-bottom: 50px;
	@include mq($max-width: 550px) {
		margin-bottom: 20px; }
	&__title {
		font: 700 36px 'Inter', sans-serif;
		@include mq($max-width: 700px) {
			font-size: 26px; }
		@include mq($max-width: 575px) {
			font-size: 20px; } }
	&__subtitle {
		font: 700 20px 'Inter', sans-serif;
		margin-bottom: 20px;
		@include mq($max-width: 700px) {
			font-size: 15px; } }
	&__text {
		font: 400 17px/1.4 'Inter', sans-serif;
		@include mq($max-width: 700px) {
			font-size: 16px; } }
	&__line {
		display: block;
		width: 130px;
		height: 2px;
		background: $black;
		margin: 15px 0; } }

.page-container,
#new-landing {
	width: 100%;
	flex: 1 0 auto;
	height: auto;
	position: relative;
	overflow: hidden; }

#new-landing .container {
	max-width: 1170px;
	padding: 0 15px;
	margin: 0 auto;
	height: auto;
	// @include mq($max-width: 1500px)
	// 	max-width: 1200px
	// @include mq($max-width: 380px)
 }	// 	padding: 0 5px

#new-landing .line {
	display: block;
	width: 136px;
	height: 2px;
	background: $gray1;
	margin-bottom: 15px;
	@include mq($max-width: 700px) {
		margin-left: auto;
		margin-right: auto; } }

#new-landing .copyright {
	text-align: center;
	padding: 10px 0;
	font: 300 13px 'Inter', sans-serif;
	color: $gray5;
	a {
		color: $gray5;
		text-transform: uppercase;
		&:hover {
			color: $red; } } }
